<template>
  <div style="height: 100%;">
        <div 
          v-for="(box, index) in formVal.g"
          :key="index"
          :style="{'--width': formVal.g}"
          class="bigBox"
          ref="2"
          >
            <div
            v-for="(count, index) in formVal.c"
            :key="formVal.c+index"
            :style="{'--width': c}"
            class="minBox"
            :class="arrList[parseInt((count-1)+(box-1)*formVal.c)]?.isActive ? 'minAc' : 'minH'"
            @click="onClick(box-1,count-1,true)"
            @contextmenu.prevent.stop="rightClick(box-1,count-1)">
            <p v-if="arrList[parseInt((count-1)+(box-1)*formVal.c)]?.isActive" :style="{'--width': c, '--color': arrList[parseInt((count-1)+(box-1)*formVal.c)]?.value}">
                {{ arrList[parseInt((count-1)+(box-1)*formVal.c)]?.boom ? 'boom' : arrList[parseInt((count-1)+(box-1)*formVal.c)]?.value}}
            </p>
            <p v-if="arrList[parseInt((count-1)+(box-1)*formVal.c)]?.right" class="rightP" :style="{'--width': c}">
                {{'boom'}}
            </p>
            </div>
            <br>
        </div>
        <p class="showTop">{{ac}}</p>
        <div v-show="isShow" class="black"/>
  </div>
</template>

<script>
export default {
 name: 'gameV',
 data () {
     return {
        //  c: this.formVal.c,
         arrBoom: [],
         arrList: [],
         isActive: true,
         isShow: false,
         ac: 0
     }
 },
created() {
     this.randomBoom()
     this.allList()
 },
 props: [
     'formVal',
     'sta'
 ],
 watch: {
     sta: {
       immediate: true,
       handler() {
            this.isActive = true
            this.randomBoom()
            this.allList()
       }  
     }
 },
 computed: {
     c() {
         this.randomBoom(true)
         this.allList()
         return this.formVal.c
     }
 },

 methods: {
     randomBoom(a) {
         this.isActive = a
         if (this.isActive) {
             this.ac = 0
             this.isShow = false
             let arrList = []
            while (arrList.length < this.formVal.boom) {
                let num = Math.floor(Math.random() * this.formVal.c * this.formVal.g);
                let obj = {c: num%this.formVal.c,g: parseInt(num/this.formVal.c),num}
                if (!arrList.length) {
                    arrList.push(JSON.parse(JSON.stringify(obj)))
                } else if (!arrList.find((ele) => ele.num == num)) {
                    arrList.push(JSON.parse(JSON.stringify(obj)))
                }
            }
            this.arrBoom = arrList
            // arrList.sort((a,b) => {
            //     return a.num - b.num
            // })
            // arrList.forEach(ele => {
            //     console.log(ele.num);
            // })
            // this.isActive = false
         }
     },
     // 点击后的操作
     onClick(g,c,cl) {
         if (cl) {
             this.ac++
         }
        //  let refsNmae = 'minBox'+parseInt(c+g*this.formVal.c)
        //  this.$refs[refsNmae][0].style.backgroundColor='black'
        if (!this.arrList[c+g*this.formVal.c].isActive) {
            this.arrList[c+g*this.formVal.c].isActive = true
            this.arrList[c+g*this.formVal.c].right = false
            if (this.arrList[c+g*this.formVal.c].boom) {
                    this.isShow = true
                this.$confirm('游戏结束了，你使用了'+ this.ac +'步，是否重新开始?', '失败', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: ''
                }).then(() => {
                    this.isShow = false
                    this.isActive = true
                    this.randomBoom()
                    this.allList()
                }).catch(() => {
                    return          
                });
            } else if (this.arrList[c+g*this.formVal.c].value == 0) {
                let arr = this.arr8(c+g*this.formVal.c,c)
                arr.forEach(ele => {
                    let ch= ele%this.formVal.c
                    let ga= parseInt(ele/this.formVal.c)
                    this.onClick(ga,ch,false)
                })
            }
            let win = this.arrList.find(ele => {
                return ele.boom != true && ele.isActive != true
            })
            if (!win) {
                this.$confirm('游戏结束了，你使用了'+ this.ac +'步，是否重新开始?', '胜利', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: ''
                }).then(() => {
                    this.isShow = false
                    this.isActive = true
                    this.randomBoom()
                    this.allList()
                }).catch(() => {
                    return          
                });
            }
        }
     },
     //周围8个的属数组
     arr8(n,ch) {
         let c = this.formVal.c
         let g = this.formVal.g
         if (n < c) {
             if (!ch) {
                 return [n+1+c, n+1, n+c]
             } else if(ch == c-1){
                 return [n+c-1, n-1, n+c]
             } else {
                 return [n+1+c, n+c, n+c-1, n-1, n+1]
             }
         } else if (c*g-1-n<c) {
             if (!ch) {
                 return [n+1-c, n+1, n-c]
             } else if(ch == c-1){
                 return [n-c-1, n-1, n-c]
             } else {
                 return [n+1-c, n-c, n-c-1, n-1, n+1]
             }
         } else if(!ch){
             return [n-c+1, n-c, n+1, n+c+1, n+c]
         } else if(ch == c-1) {
             return [n-c-1, n-c, n-1, n+c-1, n+c]
         } else {
             return [n-c-1, n-c, n+1-c, n-1, n+1, n+c-1, n+c, n+c+1]
         }
     },
     //右键事件
     rightClick(g,c) {
         let num = c+g*this.formVal.c
         if (!this.arrList[num].isActive) {
             this.arrList[num].right = !this.arrList[num].right
         }
     },
     //创建一个空的数组装每一个按钮
    allList() {
        if (this.isActive) {
            let arr = []
            let obj = { isActive : false, value: 0, boom: false, right: false}
            for (let i = 0; i < this.formVal.c*this.formVal.g; i++) {
                arr.push(JSON.parse(JSON.stringify(obj)))
            }
            this.arrList = arr
            this.countNum()
            this.isActive = false
        }
     },
     //计算每个按钮的值
     countNum() {
         if (this.arrBoom.length > 0) {
             this.arrBoom.forEach(element => {
                 if (this.arrList.length) {
                    this.arrList[element.num].boom = true
                    if (element.num < this.formVal.c) {
                        this.arrList[element.num+this.formVal.c].value++
                        if (!element.c) {
                            this.arrList[element.num+1+this.formVal.c].value++
                            this.arrList[element.num+1].value++
                        } else if (element.c == this.formVal.c-1) {
                            this.arrList[element.num-1+this.formVal.c].value++
                            this.arrList[element.num-1].value++
                        } else {
                            this.arrList[element.num-1+this.formVal.c].value++
                            this.arrList[element.num+1+this.formVal.c].value++
                            this.arrList[element.num+1].value++
                            this.arrList[element.num-1].value++
                        }
                    } else if (this.formVal.c*this.formVal.g-1-element.num < this.formVal.c) {
                        if (!element.c) {
                            this.arrList[element.num-this.formVal.c].value++
                            this.arrList[element.num+1-this.formVal.c].value++
                            this.arrList[element.num+1].value++
                        }
                        else if (element.c == this.formVal.c-1) {
                            this.arrList[element.num-1-this.formVal.c].value++
                            this.arrList[element.num-this.formVal.c].value++
                            this.arrList[element.num-1].value++
                        } else {
                            this.arrList[element.num-1-this.formVal.c].value++
                            this.arrList[element.num-this.formVal.c].value++
                            this.arrList[element.num+1-this.formVal.c].value++
                            this.arrList[element.num+1].value++
                            this.arrList[element.num-1].value++
                        }
                    } else if (!element.c) {
                        this.arrList[element.num-this.formVal.c].value++
                        this.arrList[element.num+1-this.formVal.c].value++
                        this.arrList[element.num+this.formVal.c].value++
                        this.arrList[element.num+1+this.formVal.c].value++
                        this.arrList[element.num+1].value++
                    } else if (element.c == this.formVal.c-1) {
                        this.arrList[element.num-1-this.formVal.c].value++
                        this.arrList[element.num-this.formVal.c].value++
                        this.arrList[element.num-1+this.formVal.c].value++
                        this.arrList[element.num+this.formVal.c].value++
                        this.arrList[element.num-1].value++
                    } else {
                        this.arrList[element.num-this.formVal.c].value++
                        this.arrList[element.num+1-this.formVal.c].value++
                        this.arrList[element.num+this.formVal.c].value++
                        this.arrList[element.num+1+this.formVal.c].value++
                        this.arrList[element.num+1].value++
                        this.arrList[element.num-1-this.formVal.c].value++
                        this.arrList[element.num-1+this.formVal.c].value++
                        this.arrList[element.num-1].value++
                    }
                    
                 }
             });
         }
     }
 }
}
</script>

<style lang="less" scoped>
.bigBox {
    height: calc(100%/var(--width) - 2px);
    .minBox {
        width: calc(100%/var(--width) - 2px);
        // width: var(--width);
        height: 100%;
        background-color: #409EFF;
        float: left;
        margin: 1px;
        
    }
    .minH:hover {
        background-color: rgb(140, 197, 255);
    }
    .minAc {
        background-color: rgba(255, 255, 255, 0.288);
        p {
            font-size: calc(50px - var(--width)*1.5px);
            margin: 0;
            position: relative;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
        }
    }   
}
.rightP{
    font-size: calc(50px - var(--width)*1.5px);
    margin: 0;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    color: rgb(255, 255, 255);
}
.black {
    width: 100%;
    height: 100%;
    position: relative;
    top: -98%;
    right: 0%;
    background-color: rgba(206, 206, 206, 0.44);
}
.showTop {
    position: fixed;
    top: 0px;
    right: 0px;
    margin: 0px;
}
</style>