<template>
  <div>
        <el-form label-position="left" label-width="40px" :model="formVal">
            <el-form-item label="长度">
                <el-input-number v-model="formVal.c" :min="5" :max="30"></el-input-number>
            </el-form-item>
            <el-form-item label="高度">
                <el-input-number v-model="formVal.g" :min="5" :max="30"></el-input-number>
            </el-form-item>
            <!-- <el-form-item label="钥匙">
                <el-input-number v-model="formVal.key" :min="1" :max="parseInt(formVal.c*formVal.g/20)"></el-input-number>
            </el-form-item>
            <el-form-item label="炸弹">
                <el-input-number v-model="formVal.boom" :min="1" :max="parseInt(formVal.c*formVal.g/20)"></el-input-number>
            </el-form-item> -->
            <div>
                <el-button type="primary" @click="dialogVisible = true">更多设置</el-button>
                <p>默认设置一个炸弹和钥匙</p>
            </div>
            <div>
                <el-button type="primary" @click="onSubmit">确认</el-button>
                <el-button @click="onCancel">取消</el-button>
            </div>
        </el-form>
        <el-dialog
            title="更多设置"
            :visible.sync="dialogVisible"
            width="30%">
            <el-form label-position="left" label-width="40px" :model="formVal">
                <el-form-item label="钥匙">
                    <el-input-number v-model="moreFV.key" :min="1" :max="parseInt(formVal.c*formVal.g/20)"></el-input-number>
                </el-form-item>
                <el-form-item label="炸弹">
                    <el-input-number v-model="moreFV.boom" :min="1" :max="parseInt(formVal.c*formVal.g/20)"></el-input-number>
                </el-form-item>
                <el-form-item label="是否使用道具" label-width="150px">
                    <el-switch
                        v-model="moreFV.isUse"
                        active-text="是"
                        inactive-text="否">
                    </el-switch>
                </el-form-item>
                <el-form-item label="找钥匙" v-show="moreFV.isUse" label-width="80px">
                    <el-input-number v-model="moreFV.FK" :min="0" :max="parseInt(formVal.c*formVal.g/50) || 1"></el-input-number>
                </el-form-item>
                <el-form-item label="找门" v-show="moreFV.isUse" label-width="80px">
                    <el-input-number v-model="moreFV.FD" :min="0" :max="parseInt(formVal.c*formVal.g/50) || 1"></el-input-number>
                </el-form-item>
                <el-form-item label="显示炸弹" v-show="moreFV.isUse" label-width="80px">
                    <el-input-number v-model="moreFV.SB" :min="0" :max="parseInt(formVal.c*formVal.g/50) || 1"></el-input-number>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="changeFormVal">确 定</el-button>
            </span>
        </el-dialog>
  </div>
</template>

<script>
export default {
    data() {
        return {
            formVal: {
                c: 5,
                g: 5,
                boom: 1,
                key: 1,
                FK: 0,
                FD: 0,
                SB: 0
            },
            moreFV: {
                boom: 1,
                key: 1,
                isUse: false,
                FK: 0,
                FD: 0,
                SB: 0
            },
            dialogVisible: false,
        }
    },
    props: ['oldFormVal'],
    methods: {
        // 弹窗确定后修改formVal的数据
        changeFormVal() {
            if (this.moreFV.isUse) {
                this.formVal.boom = this.moreFV.boom
                this.formVal.key = this.moreFV.key
                this.formVal.FK = this.moreFV.FK
                this.formVal.FD = this.moreFV.FD
                this.formVal.SB = this.moreFV.SB
            } else {
                this.formVal.boom = this.moreFV.boom
                this.formVal.key = this.moreFV.key
                this.formVal.FK = 0
                this.formVal.FD = 0
                this.formVal.SB = 0
                this.moreFV.FK = 0
                this.moreFV.FD = 0
                this.moreFV.SB = 0
            }
            this.dialogVisible = false
            this.onSubmit()
        },
        onSubmit() {
      if (this.formVal.c && this.formVal.g && this.formVal.boom && this.formVal.key && this.formVal.FD>=0 && this.formVal.FK>=0 && this.formVal.SB>=0) {
        if (this.formVal.FD > 1 && this.formVal.FD > parseInt(this.formVal.c*this.formVal.g/50)) {
            this.$message({
                message: '找门太多，我已为你减少',
                type: 'warning'
            });
            if (parseInt(this.formVal.c*this.formVal.g/50 < 1)) {
                this.formVal.FD = 1
                this.moreFV.FD = 1
            } else {
                this.formVal.FD = parseInt(this.formVal.c*this.formVal.g/50)
                this.moreFV.FD = parseInt(this.formVal.c*this.formVal.g/50)
            }
        }
        if (this.formVal.FK > 1 && this.formVal.FK > parseInt(this.formVal.c*this.formVal.g/50)) {
            this.$message({
                message: '找钥匙太多，我已为你减少',
                type: 'warning'
            });
            if (parseInt(this.formVal.c*this.formVal.g/50 < 1)) {
                this.formVal.FK = 1
                this.moreFV.FK = 1
            } else {
                this.formVal.FK = parseInt(this.formVal.c*this.formVal.g/50)
                this.moreFV.FK = parseInt(this.formVal.c*this.formVal.g/50)
            }
        }
        if (this.formVal.SB > 1 && this.formVal.SB > parseInt(this.formVal.c*this.formVal.g/50)) {
            this.$message({
                message: '显示炸弹太多，我已为你减少',
                type: 'warning'
            });
            if (parseInt(this.formVal.c*this.formVal.g/50 < 1)) {
                this.formVal.SB = 1
                this.moreFV.SB = 1
            } else {
                this.formVal.SB = parseInt(this.formVal.c*this.formVal.g/50)
                this.moreFV.SB = parseInt(this.formVal.c*this.formVal.g/50)
            }
        }
        // this.oldFormVal = JSON.parse(JSON.stringify(this.formVal))
        this.$emit('setForm',this.formVal)
        this.sta = !this.sta
      } else {
        this.$message({
          message: '必须有值',
          type: 'warning'
        });
        this.formVal = JSON.parse(JSON.stringify(this.oldFormVal))
      }
    },
    onCancel() {
      this.formVal = JSON.parse(JSON.stringify(this.oldFormVal))
    }
    }
}
</script>

