<template>
  <div style="height: 100%;">
        <div 
          v-for="(box, index) in formVal.g"
          :key="index"
          :style="{'--width': formVal.g}"
          class="bigBox"
          ref="2"
          >
            <div
            v-for="(count, index) in formVal.c"
            :key="formVal.c+index"
            :style="{'--width': c}"
            class="minBox"
            :class="arrList[parseInt((count-1)+(box-1)*formVal.c)]?.isActive ? 'minAc' : 'minH'"
            @click="onClick(box-1,count-1,true)">
            <p v-if="arrList[parseInt((count-1)+(box-1)*formVal.c)]?.isActive" :style="{'--width': c}">
                {{ arrList[parseInt((count-1)+(box-1)*formVal.c)]?.value}}
            </p>
            </div>
            <br>
        </div>
        <p class="showTop">{{ac}}</p>
        <div v-show="isShow" class="black"/>
  </div>
</template>

<script>
export default {
 name: 'gameV',
 data () {
     return {
        //  c: this.formVal.c,
         arrG: [],
         arrList: [],
         isActive: true,
         isShow: false,
         ac: 0
     }
 },
created() {
     this.randomG()
     this.allList()
 },
 props: [
     'formVal',
     'sta'
 ],
 watch: {
     sta: {
       immediate: true,
       handler() {
            this.isActive = true
            this.randomG()
            this.allList()
       }  
     }
 },
 computed: {
     c() {
         this.randomG(true)
         this.allList()
         return this.formVal.c
     }
 },

 methods: {
    //生成随机炸弹钥匙等
     randomG(a) {
         this.isActive = a
         if (this.isActive) {
             this.ac = 0
             this.isShow = false
             let arrList = []
            while (arrList.length < this.formVal.boom + this.formVal.key +1 + this.formVal.FK + this.formVal.FD + this.formVal.SB) {
                let num = Math.floor(Math.random() * this.formVal.c * this.formVal.g);
                let obj = {c: num%this.formVal.c,g: parseInt(num/this.formVal.c),num}
                if (!arrList.length) {
                    arrList.push(JSON.parse(JSON.stringify(obj)))
                } else if (!arrList.find((ele) => ele.num == num)) {
                    arrList.push(JSON.parse(JSON.stringify(obj)))
                }
            }
            this.arrG = arrList
         }
     },
     // 点击后的操作
     onClick(g,c,cl) {
         if (cl) {
             this.ac++
         }
        //  let refsNmae = 'minBox'+parseInt(c+g*this.formVal.c)
        //  this.$refs[refsNmae][0].style.backgroundColor='black'
        if (!this.arrList[c+g*this.formVal.c].isActive) {
            this.arrList[c+g*this.formVal.c].isActive = true
            this.arrList[c+g*this.formVal.c].right = false
            if (this.arrList[c+g*this.formVal.c].boom) {
                this.isShow = true
                this.$confirm('游戏结束了，你使用了'+ this.ac +'步，是否重新开始?', '失败', {
                    confirmButtonText: '确定',
                    type: ''
                }).then(() => {
                    this.isShow = false
                    this.isActive = true
                    this.randomG()
                    this.allList()
                }).catch(() => {
                    return          
                });
            } else if (this.arrList[c+g*this.formVal.c].FK) {
                //找钥匙功能
                alert("帮不了一点")
            } else if (this.arrList[c+g*this.formVal.c].FD) {
                //找门功能
                alert("帮不了一点")
            } else if (this.arrList[c+g*this.formVal.c].SB) {
                //显示炸弹功能
                this.arrList.forEach(el => {
                    if (el.boom && !el.isActive) {
                        el.isActive = true
                        return
                    }
                })
            }
            let win = this.arrList.find(ele => {
                return ele.key == true && ele.isActive == false
            })
            let win2 = this.arrList.find(ele => {
                return ele.dor == true && ele.isActive == true
            })
            if (!win && win2) {
                this.$confirm('游戏结束了，你使用了'+ this.ac +'步，是否重新开始?', '胜利', {
                    confirmButtonText: '确定',
                    type: ''
                }).then(() => {
                    this.isShow = false
                    this.isActive = true
                    this.randomG()
                    this.allList()
                }).catch(() => {
                    return          
                });
            }
        }
     },
     //创建一个空的数组装每一个按钮
    allList() {
        if (this.isActive) {
            let arr = []
            let sta = 0
            for (let i = 0; i < this.formVal.c*this.formVal.g; i++) {
                let obj = { isActive : false, value: '', boom: false, key: false, dor: false, FK: false, FD: false, SB: false}
                this.arrG.forEach(ele => {
                    if (ele.num == i) {
                        if (!sta) {
                            obj.dor = true
                            obj.value = '门'
                            sta++
                        } else if (sta < this.formVal.boom+1) {
                            obj.boom = true
                            obj.value = 'boom'
                            sta++
                        } else if (sta < this.formVal.boom+1+this.formVal.key){
                            obj.key = true
                            obj.value = 'key'
                            sta++
                        } else if (sta < this.formVal.boom+1+this.formVal.key+this.formVal.FK) {
                            obj.FK = true
                            obj.value = 'FK'
                            sta++
                        } else if (sta < this.formVal.boom+1+this.formVal.key+this.formVal.FK+this.formVal.FD) {
                            obj.FD = true
                            obj.value = 'FD'
                            sta++
                        } else if (sta < this.formVal.boom+1+this.formVal.key+this.formVal.FK+this.formVal.FD+this.formVal.SB) {
                            obj.SB = true
                            obj.value = 'SB'
                            sta++
                        } 
                    }
                });
                arr.push(JSON.parse(JSON.stringify(obj)))
            }
            this.arrList = arr
            this.isActive = false
        }
     }
     
 }
}
</script>

<style lang="less" scoped>
.bigBox {
    height: calc(100%/var(--width) - 2px);
    .minBox {
        width: calc(100%/var(--width) - 2px);
        // width: var(--width);
        height: 100%;
        background-color: #409EFF;
        float: left;
        margin: 1px;
        
    }
    .minH:hover{
        background-color: rgb(102, 177, 255);
    }
    .minAc {
        background-color: rgba(255, 255, 255, 0.288);
        p {
            font-size: calc(50px - var(--width)*1.5px);
            margin: 0;
            position: relative;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
        }
    }   
}
.rightP{
    font-size: calc(50px - var(--width)*1.5px);
    margin: 0;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    color: rgb(255, 255, 255);
}
.black {
    width: 100%;
    height: 100%;
    position: relative;
    top: -98%;
    right: 0%;
    background-color: rgba(206, 206, 206, 0.44);
}
.showTop {
    position: fixed;
    top: 0px;
    right: 0px;
    margin: 0px;
}
</style>