<template>
  <div>
        <el-form label-position="center" label-width="40px" :model="formVal">
            <el-form-item label="长度">
                <el-input-number v-model="formVal.c" :min="5" :max="30"></el-input-number>
            </el-form-item>
            <el-form-item label="高度">
                <el-input-number v-model="formVal.g" :min="5" :max="30"></el-input-number>
            </el-form-item>
            <el-form-item label="数量">
                <el-input-number v-model="formVal.boom" :min="parseInt(formVal.c*formVal.g/10)" :max="parseInt(formVal.c*formVal.g/5*4)"></el-input-number>
            </el-form-item>
            <div>
                <el-button type="primary" @click="onSubmit">确认</el-button>
                <el-button @click="onCancel">取消</el-button>
            </div>
        </el-form>
  </div>
</template>

<script>
export default {
    data() {
        return {
            formVal: {
                c: 5,
                g: 5,
                boom: 2
            }
        }
    },
    props: ['oldFormVal'],
    methods: {
        onSubmit() {
      if (this.formVal.c && this.formVal.g && this.formVal.boom) {
        if (this.formVal.boom < parseInt(this.formVal.c*this.formVal.g/10)) {
          this.$message({
            message: '炸弹太少，我已为你添加更多',
            type: 'warning'
          });
          this.formVal.boom = parseInt(this.formVal.c*this.formVal.g/10)
        } else if (this.formVal.boom > parseInt(this.formVal.c*this.formVal.g/5*4)) {
            this.$message({
                message: '炸弹太多，我已为你减少',
                type: 'warning'
            });
            this.formVal.boom = parseInt(this.formVal.c*this.formVal.g/5*4)
        }
        // this.oldFormVal = JSON.parse(JSON.stringify(this.formVal))
        this.$emit('setForm',this.formVal)
        this.sta = !this.sta
      } else {
        this.$message({
          message: '必须有值',
          type: 'warning'
        });
        this.formVal = JSON.parse(JSON.stringify(this.oldFormVal))
      }
    },
    onCancel() {
      this.formVal = JSON.parse(JSON.stringify(this.oldFormVal))
    }
    }
}
</script>

