<template>
  <div class="bigBox">
    <div class="left">
      <div class="top">
        <el-row class="topRow">
          <el-col :span="24" class="topBut"><el-button type="primary" @click="start">开始游戏</el-button></el-col>
          <el-col :span="24" class="topBut"><el-button type="warning" @click="game = !game">更换游戏</el-button></el-col>
          <el-col :span="24" class="topBut"><el-button type="danger" @click="status = 0">游戏设置</el-button></el-col>
          <el-col :span="24" class="topBut"><el-button type="info" @click="status = 1">游戏说明</el-button></el-col>
          <el-col :span="24"><el-button type="success" @click="goHome">返回主页</el-button></el-col>
        </el-row>
      </div>
      <hr>
      <div class="bottom" >
        <div v-show="status == 0">
          <p>游戏设置</p>
          <SlStatu v-if="!game" :oldFormVal='formVal' @setForm="setForm"/>
          <ZmStatu v-else :oldFormVal="formZm" @setForm="setZm"/>
        </div>
        <div v-show="status == 1">
          <p>游戏说明</p>
          <p v-if="!game">游戏为自制扫雷，玩法基本一致</p>
          <p v-else>游戏规则是，找到门所在的位置，并找到所有的钥匙才能通过，如果踩到炸弹则会失败</p>
        </div>
      </div>
    </div>
    <div class="right">
      <GameV v-if="!game" :formVal="formVal" :sta="sta"/>
      <GameZ v-else :formVal="formZm" :sta="sta"/>
    </div>
  </div>
</template>

<script>
import ZmStatu from './components/zmStatu.vue'
import SlStatu from './components/slStatu.vue'
import GameV from './components/gameV.vue'
import GameZ from './components/gameZ.vue'
export default {
  name: 'mClearance',
  data () {
    return {
      formVal: { c: 5, g: 5, boom: 2},
      formZm: { c:5, g: 5, boom: 1, key: 1},
      status: 0,
      sta: true,
      game: false,
      num: ''
    }
  },
  components: {
    GameV,
    GameZ,
    SlStatu,
    ZmStatu
  },
  methods: {
    start() {
      this.sta = !this.sta
    },
    setForm(val) {
      this.formVal = JSON.parse(JSON.stringify(val))
    },
    setZm(val) {
      this.formZm = JSON.parse(JSON.stringify(val))
    },
    goHome() {
      this.$router.push({name: "home",params: {num: this.num}})
    }
  },
  created() {
    this.num = this.$route.params.num
  }
}
</script>

<style lang="less" scoped>
.bigBox {
  width: calc(100% - 30px);
  height: calc(100% - 30px);
  padding: 15px;
  background-color: rgba(197, 197, 197, 0.295);
  .left {
    background-color: rgba(197, 197, 197, 0.486);
    height: 100%;
    width: 20%;
    float: left;
    .top {
      width: 100%;
      height: 48%;
      .topRow {
        padding: 20px;
        .topBut {
          margin-bottom: 25px;
        }
      }
    }
  }
  .right {
    height: calc(100% - 6px);
    width: calc(80% - 6px);
    float: right;
    background-color: rgba(197, 197, 197, 0.486);
    border: 3px solid rgba(163, 163, 163, 0.486);
  }
}
</style>